<template>
  <div id="app">
    <div id="nav">
      <nav-bar :shoplist="shoplist" @submit="zhi"></nav-bar>
    </div>
                                  <!--data是数据绑定  -->
    <router-view @chuanzhi="ziji" :data="data" :arr="arr" :index="index" :shoplist="shoplist"/>
  </div>
</template>
<script>
import NavBar from "./components/NavBar";
import project from './assets/home/project.json'
export default {
  data() {
    return {
      shoplist: [],
      data:'',
      goodsList:'',
      list:[],
      arr:[],
      index:''
    };
  },
  components: {
    NavBar,
  },
   created(){
        this.goodsList=project.product
    },
 
  methods: {
    ziji(it) {
      // console.log('shoplist1 ==> ', this.shoplist1)
      // console.log("it ==> ", it);
      it = JSON.parse(JSON.stringify(it));
      for (let i = 0; i < this.shoplist.length; i++) {
        if (this.shoplist[i].id == it.id) {
          this.shoplist[i].count += it.count;
          // console.log("shoplist ==> ", this.shoplist);
          return;
        }
      }
      this.shoplist.push(it);
    },
    zhi(e){
      // 每次回车都能获取值,在search页面有获取方法
      this.data=e;
      // console.log(e);
    }
  },
   watch:{
        data:function(){
        // this.list=this.data;
      let val=this.data
       if(val==''){
        return
      }
        this.arr=[]
      this.goodsList.forEach(it=>{
        it.forEach(item=>{
          if(item.nickname.search(val)!=-1){
            this.arr.push(item)
          }
        })
      })
    },
    arr:function(){
      this.index=this.arr.length
      // console.log(this.index);
    }
    },
};
</script>
<style lang="less">
  .mouse-cover-canvas{
        // width: 400px !important;
        left: 780px !important;
    }
 
</style>


