import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
//获取原型对象上的push函数
const originalPush = VueRouter.prototype.push
//修改原型对象中的push方法
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpa2ckChunkName: "about" */ '../views/About.vue')
  },
  {
    path:'/shopstore',
    name:"ShopStore",
    component:()=>import("../views/ShopStore")
  },
  {
    path:'/productdisplay',
    name:'ProductDisplay',
    component:()=>import("../components/ProductDisplay.vue")
  },
  {
    path:'/search',
    name:'Search',
    component:()=>import("../components/Search.vue")
  },
  {
    path:'/shoppingcar',
    name:'ShoppingCar',
    component:()=>import("../views/ShoppingCar.vue")
  },
  {
    path:'/contact',
    name:'Contact',
    component:()=>import("../views/Contact.vue")
  },
  {
    path:'/provide',
    name:'Provide',
    component:()=>import("../components/Provide.vue")
  },
  {
    path:'/team',
    name:'Team',
    component:()=>import("../components/Team.vue")
  }
]

const router = new VueRouter({
  routes
})

export default router
