<template>
    <div class="shopcar">
       <div class="shop1" v-for="(item,index) in shoplist" :key="index">
           <div class="shop2" :class="{'close':index==1}">
               <img :src="item.img" alt="">
               <div class="shop3">
                   <p>{{item.nickname}}</p>
                   <p>￥{{item.money}}</p>
                   <p>数量：{{item.count}}</p>
               </div>
               <i class="el-icon-close" @click="close(index)"></i>
           </div>
       </div>
       <p v-show="isshow" class="zongjia"><span>总价：</span><span>￥{{totalPrice}}</span></p>
    </div>
</template>
<script>
export default {
   data(){
       return{
           list:[],
           index:0,
            isshow:true
       }
   },
    props:{
        shoplist:[Array]
    },
    methods:{
        close(index){
              this.shoplist.splice(index,1)
        },
    },
    computed:{
        totalPrice:function(){
            var totalPrice=0;
            var len=this.shoplist.length;
            for(var i=0;i<len;i++){
                totalPrice+=this.shoplist[i].money*this.shoplist[i].count;
            }
            return totalPrice;
        }
    },
}
</script>
<style lang="less" scoped>
.shopcar{
    width: 310px;
    // height: 250px;
    background: white;
    box-shadow: 1px 1px 5px #888;
    position: absolute;
    z-index: 10;
    margin-top: 25px;
    margin-left: -280px;
    display: none;
    .zongjia{
        width: 90%;
        margin: 0 auto;
        display: flex;
        font-size: 18px;
        height: 50px;
        line-height: 50px;
        justify-content: space-between;
    }
    .shop1{
      .close{
          display: none;
      }
        .shop2{
            display: flex;
            width: 90%;
            margin: 0 auto;
            margin-top: 20px;
            padding-bottom: 20px;
            border-bottom: 1px solid #535353;
            justify-content: space-around;
            img{
                width: 30%;
            }
            .el-icon-close{
                margin-top: -12px;
            }
            .shop3{
                width: 60%;
                display: flex;
                flex-direction: column;
                justify-content: space-evenly;
                margin-left: 10px;
                p:nth-child(1){
                    font-size: 16px;
                    color: #535353;
                }
                p:nth-child(2){
                    font-size: 16px;
                    color: #535353;
                    font-weight: bold;
                }
                p:nth-child(3){
                    font-size: 13px;
                    color: #535353;
                }
            }
        }
    }
    }
</style>