<template>
    <div class="mask">
        <div class="tanchaung">
            <p><i  class="el-icon-close" @click="changeisshow"></i></p>
            <div class="fle">
                <img :src="it.img" alt="">
                <div>
                    <p>{{it.nickname}}￥{{it.money}}</p>
                    <p>
                        <i class="el-icon-star-on"></i>
                        <i class="el-icon-star-on"></i>
                        <i class="el-icon-star-on"></i>
                        <i class="el-icon-star-on"></i>
                        <i class="el-icon-star-on"></i>
                    </p>
                    <p>{{it.tro}}</p>
                    <p><span>类别：</span>护肤品</p>
                    <p><span>标签：</span>时尚</p>
                    <p>
                        <button @click="num--">-</button>
                        <input v-model="num" disabled="disabled"/>
                        <button @click="num++">+</button>
                        <span  @click="submit">添加到购物车<i class="el-icon-shopping-cart-2"></i></span>
                    </p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props:{
        it:[Object,String,Array],
    },
    data(){
        return{
            num:1
        }
    },
    watch:{
        num:function(){
            if(this.num<=0){
                this.num=1
            }
        }
    },
    created(){
    },
    methods:{
        changeisshow(){
            this.$emit("change",false)
        },
        submit(){
            this.it.count=this.num;
            this.num=1;
            this.$emit('chuanzhi',this.it)
        }
    }
}
</script>   
<style lang="less" scoped>
.mask{
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    z-index: 10;
    justify-content: center;
    align-items: center;
   
    .tanchaung{
        width: 1000px;
        height: 650px;
        background:white;
        z-index:16;
        opacity: 1;
        p{
            text-align: right;
            font-size: 25px;
            i{
                margin-right: 10px;
            }
        }
        .fle{
            width: 100%;
           display: flex;
           justify-content: space-around;
           div{
               display: flex;
               flex-direction: column;
               width: 500px;
               font-size: 12px;
                p{
                   text-align: left;
                   font-size: 17px;
                   margin-top: 30px;
               }
               p:nth-child(1){
                   font-size: 30px;
                   font-weight: bold;
                   margin:0;
               }
              
               p:nth-child(2){
                   font-size: 24px;
                   margin: 0;
                   i{
                       color:#faa7a3;
                   }
               }
               p:nth-child(4){
                   margin-top: 50px;
                   span{
                       font-weight: bold;
                   }
               }
               p:nth-child(5){
                   margin-top: 10px;
                   span{
                       font-weight: bold;
                   }
               }
               p:nth-child(6){
                   button{
                       width: 47px;
                       height: 47px;
                       background: white;
                       border: none;
                       border: 1px solid black;
                       &:hover{
                           background:#333333;
                           color: white;
                       }
                   }
                   input{
                       vertical-align: bottom;
                       display:inline-block;
                       width: 45px;
                       height: 45px;
                       border: 1px solid black;
                       border-left: none;
                       border-right: none;
                       text-align: center;
                   }
                   span{
                       margin-left: 20px;
                       width: 150px;
                       height: 45px;
                       display: inline-block;
                       border: 1px solid black;
                       vertical-align: bottom;
                       text-align: center;
                       position: relative;
                       line-height: 45px;
                       i{
                           position: absolute;
                           top:10px;
                           left: 160px;
                           color: white;
                            z-index: 10;
                            font-size: 25px;
                       }
                   }
                   span::after{
                       content: "";
                       position: absolute;
                       display: block;
                       width: 47px;
                       height: 47px;
                       background: #999999;
                       top: -1px;
                       left: 150px;
                   }
                   span:hover{
                       background: #000;
                       color: white;
                       cursor: pointer;
                   }
                   
               }
           }
        }
    }   
}
</style>