<template>
  <div class="nav">
    <div class="navbar">
      <div class="logo"></div>
      <ul class="ul1">
        <router-link to="/"
          ><li class="li1" @click="index = 1" :class="{ active: index == 1 }">
            首页
          </li></router-link
        >
        <li class="li1 lili" @click="index = 2" :class="{ active: index == 2 }">
          <router-link to="/about"><span>走进荷诺</span></router-link>
          <div>
            <ul class="ul2">
               <router-link to="/provide"><li>我们提供什么</li></router-link>
               <router-link to="/team"><li>我们的队伍</li></router-link>
            </ul>
          </div>
        </li>

        <router-link to="/shopstore"><li class="li1" @click="index = 3" :class="{ active: index == 3 }">店铺</li></router-link>
        <router-link to="/shoppingcar"><li class="li1" @click="index = 4" :class="{ active: index == 4 }"> 我的购物车</li></router-link>
         <router-link to="/contact"><li class="li1" @click="index = 5" :class="{ active: index == 5 }">
          联系我们
        </li>
        </router-link>
      </ul>
      <div class="icon">
        <i class="el-icon-shopping-bag-1">
          <div class="bg1">
            <shop-car :shoplist="shoplist" class="shopcar"></shop-car>
          </div>
        </i>
        <i class="el-icon-search" @click.stop="searvhshow=true">
          <div class="bg2" v-show="searvhshow">
            <p>
              <input type="text" placeholder="搜索" @change="search($event)">
              <i class="el-icon-close" @click.stop="searvhshow=false"></i>
            </p>
          </div>
        </i>
        <!-- <i class="el-icon-tickets"></i> -->
      
      </div>
    </div>
  </div>
</template>
<script>
import ShopCar from "../components/ShopCar";
import project from '../assets/home/project.json'
export default {
  data: function () {
    return {
      index: 1,
      isshow: false,
      searvhshow:false,
      goodsList:'',
      arr:[]
    };
  },
  props: {
    shoplist: [Object, String, Array],
  },
  components: {
    ShopCar,
  },
  watch: {
    shoplist1: function () {
      // console.log(this.shoplist1);
    },
    count: function () {
      // console.log(this.count);
    },
   
  },
  created(){
    this.goodsList=project.product;
  },
  methods:{
    search(e){
      let val=e.target.value
       if(val==''){
        return
      }
      this.goodsList.forEach(it=>{
        it.forEach(item=>{
          if(item.nickname.search(val)!=-1){
            this.arr.push(item)
          }
        })
        return this.arr
      })
      this.$router.push({name:'Search',params:{arr:this.arr}})
      this.$emit('submit',val)
    }
  }
};
</script>
