    width: 1vw;
    height: 10vh;
    //   background: #000;
    position: absolute;
    margin-top: -20px;
  }
  .el-icon-shopping-bag-1 .bg1:hover .shopcar {
    display: block;
  }
  i:hover {
    cursor: pointer;
  }
  .el-icon-search{
    position: relative;
    .bg2{
      width: 90vw;
      height: 10vh;
      position: absolute;
      z-index:19;
      margin-left: -85vw;
      margin-top: -5vh;
      background: white;
      p{
        width: 100%;
