<template>
  <div class="homeproduct">
    <el-carousel
      trigger="click"
      height="880px"
      indicator-position="outside"
      arrow="never"
    >
      <el-carousel-item v-for="item in product" :key="item.id">
        <div v-for="(it, index) in item" :key="index">
          <img :src="it.img" alt="" @click="jump(it)" />
          <p>{{ it.name }}</p>
          <p>{{ it.nickname }}</p>
          <p>￥{{ it.money }}</p>
          <p class="show">
            <!-- <i class="el-icon-shopping-bag-2"></i> -->
            <i class="el-icon-search" @click="play(it)"></i>
          </p>
        </div>
      </el-carousel-item>
    </el-carousel>
    <mask-te
      v-show="isshow"
      :it="it"
      @change="zidingyi($event)"
      @chuanzhi="ziji"
    ></mask-te>
  </div>
</template>
<script>
import project from "../assets/home/project.json";
import MaskTe from "../components/Mask";
// import TangChaung from "../components/TangChuang"
export default {
  data() {
    return {
      product: null,
      isshow: false,
      it: "",
    };
  },
  methods: {
    jump(it) {
      this.$router.push({ name: "ProductDisplay", params: { item: it } });
    },
    play(it) {
      this.it = it;
      // console.log(this.it);
      if (this.it == it) {
        this.isshow = true;
      }
    },
    zidingyi(e) {
      this.isshow = e;
    },
    ziji(it) {
      this.$emit("chuanzhi", it);
    },
  },
  components: {
    //   TangChaung
    MaskTe,
  },
  created() {
    this.product = project.product;
  },
};
</script>
<style lang="less" scoped>
.homeproduct {
  width: 100%;
  margin: 0 auto;
  .el-carousel {
    width: 70%;
    margin: 0 auto;
    .el-carousel__item {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      h3 {
        color: #475669;
        font-size: 1vw;
        opacity: 0.75;
        line-height: 300px;
        margin: 0;
      }
      div {
        width: 23%;
        position: relative;
        .show {
          width: 100%;
          position: absolute;
          top: 12vw;
          left: 35%;
          display: none;
          // z-index: -1;
          i {
            width: 3vw;
            height: 3vw;
            background: #ff888b;
            border-radius: 50%;
            text-align: center;
            line-height: 3vw;
            margin-left: 1vw;
            color: white;
            font-size: 1.5vw;
          }
        }

        img {
          width: 100%;
          z-index: 2;
        }
        img:hover {
          background: #000;
          opacity: 0.4;
        }
        p:nth-child(2) {
          text-align: center;
          font-size: 1.5vw;
          margin-top: 20px;
        }
        p:nth-child(3) {
          text-align: center;
          color: #8f8f8f;
          margin-top: 10px;
        }
        p:nth-child(4) {
          font-size: 2vw;
          text-align: center;
          color: #ffa6a8;
          margin-top: 10px;
        }
      }
      div:hover .show {
        display: block;
      }
    }
  }
}

